import {
  Component,
  h,
  Element,
  Prop,
  Method,
  Listen,
  Host,
  State,
} from "@stencil/core";
import {
  InputAutoComplete,
  InputType,
  RwcInputValidatorInterface,
} from "../../../../interfaces/components/basic/input/input";

/**
 * @slot left - Slot for the left side of the input field.
 * @slot right - Slot for the right side of the input field.
 */
@Component({
  tag: "rwc-input",
  styleUrl: "./input.scss",
  shadow: true,
})
export class RwcInput {
  /**
   * Reference to the input field element.
   */
  private inputElement?: HTMLInputElement;

  /**
   * Reference to the left slot element of the component
   */
  @State() private leftSlot?: HTMLSlotElement;

  /**
   * Reference to the right slot element of the component
   */
  @State() private rightSlot?: HTMLSlotElement;

  /**
   * Reference to the component's host element.
   */
  @Element() host!: HTMLRwcInputElement;

  /**
   * The name attribute of the input field.
   */
  @Prop() name: string = "";

  /**
   * Enables the auto complete for the input field.
   */
  @Prop() autoComplete: InputAutoComplete = "on";

  /**
   * The focused state of the component.
   */
  @Prop({ mutable: true }) focused: boolean = false;

  /**
   * The current value of the input field.
   */
  @Prop({ mutable: true }) value: string = "";

  /**
   * The placeholder value of the input field.
   */
  @Prop() placeholder: string = "";

  /**
   * The type of the input field.
   */
  @Prop({ reflect: true }) type: InputType = "text";

  /**
   * List of validators used for validation of the input.
   */
  @Prop() validators: Array<RwcInputValidatorInterface> = [];

  /**
   * Determines if the validation must be only triggered manually.
   */
  @Prop() manualValidation: boolean = false;

  /**
   * Sets the disabled state for the input field.
   */
  @Prop() disabled: boolean = false;

  /**
   * The label for the input field.
   */
  @Prop() label: string = "";

  /**
   * List of errors acquired after the validation.
   */
  @State() errors: Array<string> = [];

  /**
   * Called before each render call
   */
  componentWillRender(): void {
    this.host.tabIndex = 0;
  }

  /**
   * Event handler for the focus event
   */
  @Listen("focus")
  onFocus(): void {
    if (!this.disabled) {
      this.focused = true;
      this.clearErrors();
      this.inputElement?.focus();
    }
  }

  /**
   * Event handler for the blur event
   */
  @Listen("blur")
  onBlur(): void {
    this.focused = false;
    if (!this.manualValidation) {
      this.validate();
    }
  }

  /**
   * Event handler for the input event
   */
  @Listen("input")
  onInput(): void {
    this.value = this.inputElement.value;
  }

  /**
   * Validates the input of the component
   */
  @Method()
  async validate(): Promise<boolean> {
    this.setErrors([]);
    this.validators.forEach((validator: RwcInputValidatorInterface) => {
      if (!validator.validate(this.value)) {
        this.errors.push(validator.getMessage());
      }
    });
    return this.errors.length === 0;
  }

  /**
   * Clears the list of validation errors.
   */
  @Method()
  async clearErrors(): Promise<void> {
    this.setErrors([]);
  }

  /**
   * Sets the components error array to passed array of strings.
   */
  @Method()
  async setErrors(errors: Array<string>): Promise<void> {
    this.errors = errors;
  }

  /**
   * Renders the component
   */
  render() {
    return (
      <Host
        class={{
          disabled: this.disabled,
        }}
      >
        <label
          htmlFor={this.name}
          class="label"
          style={{
            display: this.name ? "block" : "none",
          }}
        >
          {this.label}
        </label>
        <div
          class={{
            "input-container": true,
            focused: this.focused,
            error: this.errors.length > 0,
          }}
        >
          <div class="slot-container">
            <div class="slot-horizontal-flex">
              <slot
                name="left"
                ref={(element: HTMLSlotElement) => (this.leftSlot = element)}
              />
            </div>
          </div>
          <input
            id={this.name}
            class={{
              input: true,
              "left-slot": this.leftSlot?.assignedElements().length > 0,
              "right-slot": this.rightSlot?.assignedElements().length > 0,
            }}
            name={this.name}
            autoComplete={this.autoComplete}
            type={this.type}
            value={this.value}
            placeholder={this.placeholder}
            disabled={this.disabled ? true : null}
            ref={(element: HTMLInputElement) => (this.inputElement = element)}
          />
          <div class="slot-container">
            <div class="slot-horizontal-flex">
              <slot
                name="right"
                ref={(element: HTMLSlotElement) => (this.rightSlot = element)}
              />
            </div>
          </div>
        </div>
        <p class="error-text">{this.errors[0] || ""}</p>
      </Host>
    );
  }
}
